import React, { useEffect } from "react";
import { useGlobalContext } from "../../context";

const Contact = () => {
  const { setPath } = useGlobalContext();
  useEffect(() => {
    setPath("contact");
  }, []);
  return (
    <section className="page">
      <h2>Contact Page</h2>
    </section>
  );
};

export default Contact;
