import React, { useEffect } from "react";
import { useGlobalContext } from "../../context";

const Error = () => {
  const { setPath } = useGlobalContext();

  useEffect(() => {
    setPath("");
  }, []);

  return (
    <section className="page">
      <h2>404 Page</h2>
    </section>
  );
};

export default Error;
