import React, { useEffect } from "react";
import { useGlobalContext } from "../../context";

const About = () => {
  const { setPath } = useGlobalContext();

  useEffect(() => {
    setPath("about");
  }, []);

  return (
    <section className="page">
      <h2>About Page</h2>
    </section>
  );
};

export default About;
