import React, { useEffect } from "react";
import { useGlobalContext } from "../../context";

const Projects = () => {
  const { setPath } = useGlobalContext();

  useEffect(() => {
    setPath("projects");
  }, []);

  return (
    <section className="page">
      <h2>Projects Page</h2>
    </section>
  );
};

export default Projects;
